import { pink } from '@mui/material/colors';

export default function Home() {

    return (
        <>
            <h1>Dnyann en gzel diisinin hasta listesi</h1>
            <Stack spacing={2} direction="column">
                <Link to="/Form" style={{ textDecoration: 'none' }}><Button variant="contained">Hasta Ekle</Button></Link>

                <Link to="/Listings" style={{ textDecoration: 'none' }}><Button variant="contained">Listeyi grntle</Button>  </Link>
            </Stack></>
    );
}